* {
    margin: 0;
    padding: 0;
}

.coverbg {
  position: absolute;
  top: 50%;
  left: 50%;
  color:aliceblue;
}

.browse_page_first {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
}
.browse_page_link {
    color: black;
    font-size: 3vw;
    width: 50vw;
    background-color: aliceblue;
}
.bg {
    width: 100%;
    height: max-content;
}

.church_img {
    position: absolute;
    cursor: crosshair;
}

.church_candle {
  position: absolute;
  width: 100%;
  height: max-content;
  top: 0%;
  cursor:grab;
}

.church_div {
    overflow-x: hidden;
    
}

.hidden {
    transform: scale(0);
    transition: transform 0.1s ease-out;
}

.sudoku {
    text-align: center;
    font-family: Arial, sans-serif;
    margin-top: 20px;
  }
  
  h1 {
    margin-bottom: 20px;
  }
  
.board {
  display: grid;
  grid-template-columns: repeat(9, 50px);  /* 9 колонок, по 50px каждая */
  grid-template-rows: repeat(9, 50px);     /* 9 строк, по 50px каждая */
  gap: 5px;                                /* Отступ между ячейками */
  justify-content: center;
  margin: 0 auto;
}
  
  .cell {
    width: 50px;
    height: 50px;
    font-size: 20px;
    text-align: center;
    border: 1px solid #ccc;
    box-sizing: border-box;
  }
  
  .cell:focus {
    outline: 2px solid blue;
  }
  
  .cell:disabled {
    background-color: lightgray;
  }

  .container {
    text-align: center;
    font-family: Arial, sans-serif;
  }
  
  .grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    padding: 20px;
    margin: auto;
  }
  
  .grid-item {
    text-align: center;
  }
  
  .grid-item img {
    width: 20vw;
    border-radius: 10px;
  }
  
  .order-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    max-width: 300px;
    margin: 20px auto;
  }
  
  .order-form input,
  .order-form select,
  .order-form button {
    width: 100%;
    padding: 10px;
    font-size: 16px;
  }
  
  .order-form button {
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .order-form button:hover {
    background-color: #0056b3;
  }

  .market-text {
    font-size: 2vw;
  }
  .market-text2 {
    font-size: 1vw;
  }

  .hidden-text {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    font-size: 3vw;
    font-weight: bold;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    color: transparent; /* Делаем текст невидимым */
    user-select: text; /* Разрешаем выделение */
  }
  
  .hidden-text::selection {
    color: black; /* Делаем текст видимым при выделении */
    background: lightgray;
  }

  body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 20px;
    background-color: #f4f4f9;
  }
  .inputgiga {
    position: absolute;
    top: 50%;
    left: 30%;
    width: 10%;
    height: 5%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 25px;
  }

  .buttongiga {    
    position: absolute;
    top: 60%;
    left: 30%;
    width: 10%;
    height: 5%;
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #45a049;
  }
    
  .correct {
    color: green;
  }
  
  .incorrect {
    color: red;
  }

  .solve {
    position: absolute;
    font-size: 4vw;
    color: aliceblue;
    top: 50%;
    left: 45%;

  }

  .FINAL_CONTAINER {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 8vw;
  }

  .final_column {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .final_row {
    display: flex;
    flex-direction: row;
  }

  .reveal6, .revealN, .reveal0, .revealM, .reveal3, .revealD, .revealGNOME {
    opacity: 0;
    transition: opacity 0.9s ease-in-out;
}

.reveal6.show, .revealN.show, .reveal0.show, 
.revealM.show, .reveal3.show, .revealD.show, .revealGNOME.show {
    opacity: 1;
}

.final_hide {
  cursor: pointer;
}